.card{
    perspective: 150rem;
    -moz-perspective: 150rem;
    position: relative;
    height: 50rem;

    &__side{
        background-color: orangered;
        font-size: 2rem;
        transition: all .8s ease;
        height: 50rem;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        backface-visibility: hidden;

        &--front{
            background-color: orangered;
        }

        &--back{
            background-color: green;
            transform: rotateY(180deg);
            &-1{
                background-color: linear-gradient(to right bottom, )
            }
        }   
    }

    &:hover &__side--front{
        transform: rotateY(180deg)
    }

    &:hover &__side--back{
        transform: rotateY(0)
    }
}